a {
  text-decoration: none;
  color: #000;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 24;
}

.wrap {
  width: 100%;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 2rem;
  background: #ededed;
}
.header_title {
  flex-grow: 0;
  font-size: 1.2rem;
  margin-right: auto;
}

.nav {
  display: flex;
  list-style: none;
  position: relative;
}
.nav_item {
  padding: 0 15px;
}
@media (max-width: 799px) {
  .nav {
    display: none;
  }
}

.float_menu {
  width: 300px;
  height: 100vh;
  list-style: none;
  background: #fff;
  padding-left: 0;
  position: fixed;
  right: 0px;
  top: -20px;
  text-align: left;
  border: 1px solid #e8e8e8;
  z-index: 0;
}
.float_menu .float_item {
  display: block;
  padding: 20px;
}

.menu_btn {
  display: none;
  background: none;
  border: none;
  z-index: 1;
  position: relative;
}
@media (max-width: 799px) {
  .menu_btn {
    display: block;
  }
}

.banner {
  display: flex;
  padding: 5rem;
  background: #cbdced;
}
.banner_title {
  margin: auto;
}

.main {
  text-align: center;
}
.main_btn {
  background: #dcedfe;
  border: none;
  padding: 10px;
  margin: 15px 0;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
}
.content_item {
  padding-bottom: 5rem;
  width: 40%;
  margin: 10px;
  background: #8798a9;
}
@media (max-width: 799px) {
  .content_item {
    width: 45%;
  }
}
@media (max-width: 499px) {
  .content_item {
    width: 90%;
  }
}

.more {
  display: none;
}/*# sourceMappingURL=App.css.map */